	module.exports = {"data":{"site":{"siteMetadata":{"title":"Kostas Bariotis' Blog","description":"I'm Kostas Bariotis, a web developer, a proud wanderer and a passionate doer. My mission is to write clean and efficient code, to solve problems on the web and to learn something more.","siteUrl":"https://kostasbariotis.com"}},"markdownRemark":{"html":"","excerpt":"","frontmatter":{"date":"January 01, 1970","path":"/drafts/the-microfunctions-architecture/","tags":null,"title":"The Microfunctions architecture","draft":true}}},"pathContext":{"prev":{"excerpt":"There is a lot of noise the past few days, since Google anounced that will  retire  Google Reader. I have been using Google Reader a lot lately. Since i am not a huge fan of the traditional news media, i love to explore news and trends everyday...","html":"<p>There is a lot of noise the past few days, since Google anounced that will <a href=\"http://googleblog.blogspot.gr/2013/03/a-second-spring-of-cleaning.html\">retire</a> Google Reader. I have been using Google Reader a lot lately. Since i am not a huge fan of the traditional news media, i love to explore news and trends everyday through the internet. Google Reader was a very handfull tool on this.</p>\n<p>I knew all that time i was using the Reader, that something is wrong. Google didn't made a single update on the product, at least in the foregound. I was impressed to know that the usage was declined, i thought aren't people use such a niche app?  Are they still enjoying watching news shows on TV? Didn't they as me, turned off their TV after they used for the first time Google Reader Play. Oh yeah, another great tool. I love working with stuff in the house and having my big TV screen playing my favorites news sources with Reader Play. So great. Yet it's hard to believe that it really didn't worth it for Google to keep it. I am wondering who will take this opportunity to create something similar with more capabilities.</p>\n<p>But in the 1st of July. All this will gone. I know that people searching desparating for a replacement of their favorite news reader. Personally, i still haven't found it. And every guy that runs a RSS reader it's been the past days that has worked his ass off to attract as much users as he can that will turn off their Reader account once and for all.</p>\n<p>If you like me, still searching for a replacement, maybe this <a href=\"http://www.replacereader.com/\">initiative</a> can help. You see that there are allready a lot of apps that would do what you asked for. Try as much as you can before decide. Also help others decide too. Before it's too late!</p>","id":"/Users/Bariotis/Dev/develop/kostasbariotis.com/src/pages/still-watching-tv-news-show/index.md absPath of file >>> MarkdownRemark","timeToRead":1,"frontmatter":{"date":"2013-03-19T15:48:37.000Z","path":"/still-watching-tv-news-show/","tags":null,"title":"Still watching TV news show??","draft":null}},"next":false}}

